/*****************************************************************************/
/*
/* Projects
/*
/*****************************************************************************/
.projects {
	max-width: 750px;
	margin: 0 auto;
}

.project {
	&:nth-child(odd) {
		clear: left;
	}

	margin: 24px 24px;
	width: 315px;
	height: 315px;
	float: left;
	overflow: hidden;

	a {
		text-decoration: none;
	}

	.image {
		height: 200px; overflow: hidden;position: relative;    display: block; padding: 4px; margin-bottom: 18px; line-height: 1.42857; background-color: #f9f9f9; border: 1px solid #ddd; border-radius: 2px; -webkit-transition: all .2s ease-in-out; transition: all .2s ease-in-out;
		background-color: #EAEAEA;

		.badge {
			position: absolute;
			bottom: 0;
			right: 0;
			color: black !important;
			font-size: 24px;
			margin: 4px 4px;
			padding: 2px;
			background: rgba(255, 255, 255, 0.7);
			border-radius: 16%;
		}
	}

	.title {
		margin-bottom: 8px;
	}

	.description {
		color: #4A4A4A;
		text-decoration: none;
		margin: 0;
	}

	img.content-image {
		width: 100%;
		margin: 20px 0 0 0;
	}

	ol {
		margin-left: 50px;
	}

	.morph-content {

		h2 {
			display: none;
		}

		.modal-header {
			height: 75px;
			padding: 25px 30px 25px 40px;
			border-bottom: none;
			background: #f9f9f9;

			h2 {
				display: inline;
			}

			.icon-close {
				cursor: pointer;
			}
		}

		.button-prev, .button-next {
			width: 70px;
			right: -70px;
			top: 0;
			bottom: 0;
			position: absolute;
			z-index: 1060;
			cursor: pointer;
		}

		.button-prev {
			// right: auto;
			// left: -70px;
		}

		.content-style-text {
			padding: 40px 40px 40px;
		}
	}
}

@media (max-width: (970px)) {
	.project {
		float: none !important;
		margin: 24px auto;
	}
}
@media (max-width: (400px)) {
	.project {
		width: 240px;

		.project-title {
			width: 100%;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
		}


	}

	.morph-button-modal-1.open .morph-content, .morph-button-modal-1.active .morph-content {
		padding: 5%;
	}
}
